.worksBlock {
    min-height: 100vh;
    background-color: #645f5f;
    border:1px solid red;

}
.worksContainer{
    border:1px solid;
    align-items: center;
    flex-direction: column;
    justify-content: center;

}
.works{
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    box-sizing: border-box;


}
.title{
    background-color: #f5d86a;

}